// https://wpqw.jp/snippet/form-reset/
@charset "utf-8";

fieldset,
legend,
button {
	margin: 0;
	padding: 0;
	border: none;
	border-radius: 0;
	outline: none;
	background: none;
	-webkit-appearance: none;
	-moz-appearance: none;
	appearance: none;
}

input[type='date'],
input[type='datetime-local'],
input[type='month'],
input[type='time'] {
	margin: 0;
	padding: 4px;
	border: 1px solid #ccc;
	outline: none;
	font-size: 16px;
}

input[type='text'],
input[type='password'],
input[type='email'],
input[type='tel'],
input[type='url'],
input[type='search'],
input[type='number'],
input[type='datetime'],
input[type='week'],
input[type='textarea'],
textarea,
select {
	// margin-right: 0;
	margin-left: 0;
	// padding: 4px;
	border: 1px solid #ccc;
	// border-radius: 0;
	outline: none;
	// background: none;
	font-size: 16px;
	-webkit-appearance: none;
	-moz-appearance: none;
	appearance: none;
}

textarea {
	width: 100%;
	height: 100px;
	overflow: auto;
}

select {
	padding-right: 30px;
	background-image: url('data:image/png;base64,');
	background-repeat: no-repeat;
	background-size: 12px 12px;
	background-position: right 8px center;
}

.select {
	display: inline-block;
	position: relative;
	overflow: hidden;
	background: #fff;
}
.select select {
	width: 100%;
	padding-right: 24px;
	border: 1px solid #ccc;
	background: none;
	text-overflow: '';
	cursor: pointer;
}
.select::before {
	position: absolute;
	top: 13px;
	right: 8px;
	width: 0;
	height: 0;
	padding: 0;
	content: '';
	border-left: 6px solid transparent;
	border-right: 6px solid transparent;
	border-top: 6px solid #666;
	pointer-events: none;
}

/* radio & checkbox */
input[type='radio'],
input[type='checkbox'] {
	display: none;
}
input[type='radio'] + span,
input[type='checkbox'] + span {
	display: inline-block;
	position: relative;
	// margin-left: 8px;
	// padding: 10px 0px;
	// color: #555;
	// font-size: 1.4rem;
	// text-align: center;
	line-height: 1.25;
	cursor: pointer;
}
input[type='radio'] + span::before,
input[type='checkbox'] + span::before {
	position: absolute;
	top: 50%;
	// left: -8px;
	width: rem(16);
	height: rem(16);
	content: '';
	// margin-top: rem(-3);
	border: 1px solid #ccc;
	background: #fff;
	@include mq('tab') {
		width: rem(24);
		height: rem(24);
	}
}

/* fieldset */
fieldset {
	padding: 8px 16px;
	border: 1px solid #ccc;
}
legend {
	padding: 0 8px;
}

/* button */
input[type='submit'],
input[type='reset'],
input[type='button'],
button {
	display: inline-block;
	margin: 0;
	padding: 10px 30px;
	border: 1px solid #999;
	border-radius: 0;
	-webkit-box-sizing: content-box;
	-webkit-appearance: button;
	appearance: button;
	background: #efefef;
	color: #000;
	font-size: 20px;
	cursor: pointer;
}

/* 
---------------------------------------------------------------------------- */
/* Firefox */
button::-moz-focus-inner,
[type='button']::-moz-focus-inner,
[type='reset']::-moz-focus-inner,
[type='submit']::-moz-focus-inner {
}
button:-moz-focusring,
[type='button']:-moz-focusring,
[type='reset']:-moz-focusring,
[type='submit']:-moz-focusring {
}
/* IE */
input::-ms-clear {
}
input::-ms-reveal {
}
select::-ms-expand {
	display: none;
}
/* webkit */
[type='number']::-webkit-inner-spin-button,
[type='number']::-webkit-outer-spin-button {
}
::-webkit-file-upload-button {
}
/* iOS */
input[type='submit']::-webkit-search-decoration,
input[type='reset']::-webkit-search-decoration,
input[type='button']::-webkit-search-decoration,
input[type='search']::-webkit-search-decoration {
	display: none;
}
::-webkit-search-cancel-button {
	-webkit-appearance: none;
}
input[type='search']::-webkit-search-decoration {
	margin: 0;
	padding: 0;
	border: 0;
	border-radius: 0;
	-moz-appearance: none;
	-webkit-appearance: textfield;
	-webkit-box-sizing: border-box;
	appearance: none;
	background: none;
	outline: none;
	font-size: 16px;
}

/* 
---------------------------------------------------------------------------- */
/* hover */
input:hover,
textarea:hover,
select:hover {
	border-color: #666;
}

input[type='radio'] + span:hover,
input[type='checkbox'] + span:hover {
	color: #000;
}
input[type='radio'] + span:hover::before,
input[type='checkbox'] + span:hover::before {
	border-color: #000;
}
input[type='radio'] + span:hover::after,
input[type='checkbox'] + span:hover::after {
	// position: absolute;
	// content: '';
	// top: 50%;
	// left: rem(4);
	// width: 8px;
	// height: 8px;
	// // margin-top: rem(2);
	// background: #3498db;
}

/* checked */
input[type='radio']:checked + span,
input[type='checkbox']:checked + span {
	// color: #3498db;
	// border-color: #3498db;
}
input[type='radio']:checked + span::before,
input[type='checkbox']:checked + span::before {
	color: #171375;
	border-color: #171375;
}
input[type='radio']:checked + span::after,
input[type='checkbox']:checked + span::after {
	position: absolute;
	content: '';
	top: 50%;
	left: rem(3.5);
	transform: translate(0%, -50%);
	width: rem(8);
	height: rem(8);
	// margin-top: rem(1);
	background: #171375;
	@include mq('tab') {
		left: rem(6);
		width: rem(12);
		height: rem(12);
	}
}

/* radio */
input[type='radio'] + span::before,
input[type='radio'] + span:hover::after,
input[type='radio']:checked + span::after {
	border-radius: 50%;
}

/* button */
input[type='submit']:hover,
input[type='reset']:hover,
input[type='button']:hover,
button:hover {
	color: #fff;
}

/* focus */
input:focus,
textarea:focus {
	border-color: #3498db;
}
input[type='submit']:focus,
input[type='reset']:focus,
input[type='button']:focus,
input[type='search']:focus {
	outline-offset: -2px;
}

/* disabled */
input:disabled,
input:disabled:hover,
textarea:disabled,
textarea:disabled:hover {
	border-color: #ccc;
	background: #eee;
	cursor: not-allowed;
}
input[type='radio']:disabled + span,
input[type='checkbox']:disabled + span {
	color: #ccc;
	cursor: not-allowed;
}
input[type='radio']:disabled + span::before,
input[type='checkbox']:disabled + span::before {
	border-color: #ccc;
	cursor: not-allowed;
}

/*  */
input:valid {
}
input:invalid {
}
input:required {
}
/* placeholder */
input::-webkit-input-placeholder {
}
input::-moz-placeholder {
}
input:-ms-input-placeholder {
}
